.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.top{
  width:100%;
  display: grid;
  grid-template-columns: 45% 45%;
  justify-content: center;
}
.topl{
  padding: 10px 0px;
}
.topr{
  padding: 10px 0px;
}
.list1{
  list-style: none;
  display: grid;
  grid-template-columns:auto auto auto auto;
  justify-content: space-between;
}
.topl2{
  padding: 80px 0px 0px 0px;
}
.btn1{
  background-color: #2B8FC4;
  border:none;
  color: white;
  font-size: 16px;
  border-radius: 5px;
  padding: 10px;
  text-shadow: 0px -1px 0px #024d74;
}
.sub{
  width: 100%;
  background-color: black;
  display: grid;
  grid-template-columns:30% 30% 30%;
  justify-content: center;
  color:white;
}
.sl{
  padding: 20px 0px ;
}
.sc{
  padding: 20px 0px ;
}
.sr{
  padding: 20px 0px ;
}
h2{
  color: antiquewhite;
}
.slsub{
  display: grid;
  grid-template-columns:30% 70%;
}
.scsub{
  display: grid;
  grid-template-columns:30% 70%;
}
.srsub{
  display: grid;
  grid-template-columns:30% 70%;
}
.image{
